/*------------------filter_price----------------------------*/
.range {
  display: flex;
  justify-content: center;
}

.middle {
  position: relative;
  @media only screen and (max-width: 600px) {
    width: 90%;
  }
  width: 150px;
  margin: 10px 0;
}

.slider_filter {
  position: relative;
  z-index: 1;
  height: 5px;
  margin: 0 10px;
  @media only screen and (max-width: 600px) {
    height: 8px;
  }
}

.slider_filter > .track {
  position: absolute;
  z-index: 1;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 5px;
  background-color: rgba(49, 49, 48, 0.5);
}

.slider_filter > .range {
  position: absolute;
  z-index: 2;
  left: 0%;
  right: 0%;
  top: 0;
  bottom: 0;
  border-radius: 5px;
  background-color: rgba(49, 49, 48, 1);
}

.slider_filter > .thumb {
  position: absolute;
  z-index: 3;
  width: 10px;
  height: 10px;
  background-color: rgba(49, 49, 48, 1);
  border-radius: 50%;
  box-shadow: 0 0 0 rgba(49, 49, 48, 0.3);
  transition: box-shadow 0.3s ease-in-out;
  @media only screen and (max-width: 600px) {
    width: 20px;
    height: 20px;
    margin-bottom: 5px;
  }
}

.slider_filter > .thumb.left {
  cursor: pointer;
  left: 0%;
  transform: translate(-5px, -3px);
  @media only screen and (max-width: 600px) {
    transform: translate(-5px, -5px);
  }
}

.slider_filter > .thumb.right {
  right: 0%;
  transform: translate(5px, -3px);
  @media only screen and (max-width: 600px) {
    transform: translate(5px, -5px);
  }
}

.slider_filter > .thumb.hover {
  box-shadow: 0 0 0 15px rgba(49, 49, 48, 0.3);
}

.slider_filter > .thumb.active {
  box-shadow: 0 0 0 15px rgba(49, 49, 48, 0.5);
}

input[type="range"] {
  position: absolute;
  pointer-events: none;
  --webkit-appearance: none;
  z-index: 2;
  height: 10px;
  width: 100%;
  opacity: 0;
}

input[type="range"]::-webkit-slider-thumb {
  :hover {
    background-color: #fff;
  }
  pointer-events: all;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 0;
  border: 0 none;
  background-color: red;
  -webkit-appearance: none;
}

input[type="range"]::-webkit-slider-thumb:hover {
  background-color: #fff;
}

