@import "../../../Index.module.scss";
@import "../../../config.module.scss";

.allFilter {
  width: 200px;
  height: fit-content;
  margin-top: 10px;
  border: 1px solid #000;
  border-radius: 5px;
  padding: 10px;
}

.filterType {
  height: fit-content;
  margin-bottom: 10px;
}

.show {
  height: 200px;
}

.hideEffect {
  overflow: hidden;
  transition: all 0.2s;
  border-radius: 5px;
  @media only screen and (max-width: 600px) {
    position: fixed;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0px;
    z-index: $typeOfFilter;
  }
}

.showHidePanel {
  display: flex;
  width: 197px;
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid #000;
  border-radius: 5px;
  p {
    margin: 10px 0;
    font-size: 18px;
    margin-left: 10px;
  }
}

.showPanel {
  max-height: 500px;
  border: 2px solid #000;
  @media only screen and (max-width: 600px) {
    width: 100%;
    max-height: 100%;
    border: none;
    transform: translate(0%);
  }
}

.hidePanel {
  max-height: 0px;
  border: 2px solid #fff;
  @media only screen and (max-width: 600px) {
    max-height: 100%;
    border: none;
    transform: translate(-110%);
  }
}

.arrow {
  font-size: 25px;
  font-weight: bold;
  margin-right: 10px;
  margin-top: 5px;
  transition: all 0.2s;
}

.showArrow {
  transform: rotate(90deg);
}

.hideArrow {
  transform: rotate(-90deg);
}

.checkBlock {
  @media only screen and (max-width: 600px) {
    width: 100%;
  }
  width: 160px;
}

.checkbox_input {
  appearance: none;
  position: absolute;
  display: none;
}

.scroll {
  max-height: 330px;
  @media only screen and (max-width: 600px) {
    max-height: 90%;
    margin: 10px 0;
    padding: 0;
  }
  overflow: scroll;
  border-radius: 5px;
  padding: 10px 10px 0px 10px;
}

.scroll::-webkit-scrollbar {
  cursor: pointer;
  width: 7px;
  height: 0px;
  border-radius: 7px;
  background-color: #d8d8d8;
}

.scroll::-webkit-scrollbar-thumb {
  background-color: rgb(135, 135, 135);
  border-radius: 7px;
  &:hover {
    background-color: rgb(2, 2, 2);
  }
}

.scroll::-webkit-scrollbar-thumb:hover {
  width: 6px;
}

.checkbox_label {
  display: block;
  width: 100%;
  height: 38px;
  cursor: pointer;
  margin-bottom: 20px;
}

.checkbox_label::before {
  content: "";
  display: block;
  width: 100%;
  height: 40px;
  border: 2px solid;
  border-radius: 5px;
  z-index: 14;
}

.checkbox_label::after {
  @media only screen and (max-width: 600px) {
    margin-left: 90%;
  }
  content: "\2714";
  display: none;
  margin-top: -74px;
  margin-left: 140px;
  font-weight: bold;
}

.checkbox_input:checked + .checkbox_label::before {
  border: 3px solid;
  z-index: 2;
}

.checkbox_input:checked + .checkbox_label::after {
  display: block;
}

.content {
  display: flex;
  width: 100%;
  height: 38px;
  font-size: 18px;
  position: relative;
  top: -42px;
  border: 1px;
  padding-left: 5px;
}

@media only screen and (max-width: 600px) {
}

.slider {
  @media only screen and (max-width: 600px) {
    width: 100%;
  }
  width: 140px;
  overflow: hidden;
}

.text {
  @media only screen and (max-width: 600px) {
    width: 80%;
  }
  height: 100%;
  position: relative;
  top: 8px;
  left: 10px;
  width: 120px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@keyframes example {
  from {
    transform: translate(0px, 0);
  }
  to {
    transform: translate(-10px, 0);
  }
}
.textHover {
  width: 100%;
  overflow: visible;
  text-overflow: clip;
}

.search {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 20px 10px;
  @media only screen and (max-width: 600px) {
    padding: 0;
    margin: 10px 0 20px 0;
  }
  top: -65.5px;
  z-index: 1;
}

.button {
  width: fit-content;
  background-color: $colorTheme;
  color: #fff;
  font-weight: bold;
  font-size: 10px;
  border-radius: 5px;
  text-transform: uppercase;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  @media only screen and (max-width: 600px) {
    font-size: 16px;
  }
}
